<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                资讯详情
            </p>
        </section>
        <section class="middle">
            <p class="article-title">{{detail.title}}</p>
            <p class="time">
                <span><img width="18px" src="../../../assets/news/details_pageview_icon@2x.png">浏览量：{{detail.readCount}}</span>
                <span><img width="12px" src="../../../assets/news/details_time_icon@2x.png" alt="">{{detail.updateTime}}</span>
            </p>
        </section>
        <hr>
        <section class="bottom">
            <div class="figure">
                <img :src="detail.coverPicture" alt="">
            </div>
            <article v-html="detail.summary"></article>
        </section>
    </main>
</template>
<script>
import { getDetail, getDetailWithoutToken } from '@/apiUrl/news'
import { Icon, Toast } from 'mand-mobile'
import { mapGetters } from 'vuex'

export default {
  name: 'news_detail',
  components: {
    [Icon.name]: Icon,
    [Toast.name]: Toast
  },
  data () {
    return {
      currentId: '',
      detail: {}
    }
  },
  computed: {
    ...mapGetters([
      'status'
    ])
  },
  mounted () {
    this.currentId = this.$route.query.id
    this.getDetail()
  },
  methods: {
    getDetail () {
      if (this.status) {
        getDetail({ id: this.currentId }).then(res => {
          this.detail = res.data
        }).catch(err => {
          Toast.failed(err)
        })
      } else {
        getDetailWithoutToken({ id: this.currentId }).then(res => {
          this.detail = res.data
        }).catch(err => {
          Toast.failed(err)
        })
      }
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-bottom 0!important
        padding-top 88px
        .middle
            width 560px
            margin 50px auto
            .article-title
                font-size 32px
                color #333333
                margin-bottom 34px
            .time
                margin 0 auto
                display flex
                justify-content space-between
                font-size 24px
                span
                    display flex
                    align-items center
                    img
                        margin-right 10px
        hr
            border none
            border-bottom 1px solid #EEEEEE
            margin 30px auto
        .bottom
            padding-bottom 150px
            article
                width 560px
                margin 0 auto 30px
                color #666666
                font-size 24px
                line-height 36px
                text-align left
                text-indent 40px
            .figure
                margin 30px auto 0
                img
                    width 560px
</style>
